.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		height: 100vh;
		transition: opacity 1s ease;
		@include max-lg {
			padding: 4rem 0 8rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			border-radius: 12px;
			color: var(--color-dark);
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 35%;
			@include max-xl {
				width: 40%;
			}
			@include max-lg {
				width: 60%;
			}
			@include max-md {
				width: 100%;
			}
			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 3rem 0;
				border-radius: 500px;
				background-color: rgba($color: #fff, $alpha: 0.3);
				box-shadow: inset 25px 25px 50px rgba($color: #000000, $alpha: 0.1);
				min-height: 80%;
				width: 100%;
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				h6 {
					font-weight: 600;
				}
				.cover-description {
					padding: 2rem;
					text-align: center;
					@include max-lg {
						padding: 2rem 0 1rem;
					}
					* {
						position: relative;
					}
					.title {
						color: var(--color-dark);
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
