.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.rsvp-wrapper {
		position: relative;
		padding: 8rem 3rem;
		border-radius: 500px;
		overflow: hidden;
		@include max-md {
			padding: 5rem 2rem;
		}
		.background-element {
			&::after {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				box-shadow: inset 25px 25px 50px rgba($color: #000000, $alpha: 0.1);
				border-radius: 500px;
				content: "";
			}
		}
		.rsvp-form {
			position: relative;
			.title-section {
				&::before {
					all: unset;
				}
			}
			.form-control,
			.form-select {
				background-color: rgba($color: #fff, $alpha: 0.7);
			}
		}
	}
}
